.testimonials {
  .testimonials-title {
    margin-bottom: 10px;
    font-family: $mont;
    font-size: 30px;
    font-weight: 900;
    color: $font-black; }
  .testimonials-desc {
    position: relative;
    margin-bottom: 65px;
    font-family: $open-sans;
    font-size: 14px;
    color: $gray;
    &::after {
      content: '';
      position: absolute;
      bottom: -25px;
      left: 50%;
      transform: translateX(-50%);
      display: block;
      width: 65px;
      border-top: 1px solid $basic-color; } }


  .prev, .next {
    position: absolute;
    top: 50%;
    width: 11px;
    height: 19px;
    cursor: pointer; }
  .prev {
    left: 50%;
    transform: translate(-150px, 0);
    background-image: url("/public/src/img/left-normal.png");
    &:hover {
      background-image: url("/public/src/img/left-hover.png"); } }
  .next {
    right: 50%;
    transform: translate(150px, 0);
    background-image: url("/public/src/img/right-normal.png");
    &:hover {
      background-image: url("/public/src/img/right-hover.png"); } }

  .people-carousel {
    li {
      display: none;
      &.current, &.standby-left, &.standby-right {
        float: left;
        margin-right: -100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 300px;
        .item-name {
          font-size: 30px; }
        .item-description {
          font-size: 14px; }
        .item-role {
          font-size: 14px; } }
      &.current {
        margin-left: 33.333%;
        .item-description {
          display: block; } }
      &.standby-left {
        margin-left: 0%;
        transform: scale(0.8);
        .item-description {
          display: none; } }
      &.standby-right {
        margin-left: 66.666%;
        transform: scale(0.8);
        .item-description {
          display: none; } } } } }
